import React, { Component } from "react";
import {
  CCard,
  CCardBody,
  CRow,
  CCol,
  CButton,
  CModal,
  CModalHeader,
  CModalFooter,
  CModalTitle,
  CModalBody,
  CFormLabel,
  CBadge,
  CInputGroup,
  CFormSelect,
} from "@coreui/react-pro";
import {
  faLock,
  faSave,
  faLockOpen,
  faPlusCircle,
  faTimesCircle,
  faEdit,
  faQuestion,
} from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import NavBar from "../../navbars/ev_super_admin/nav_normal_school";
import MaterialTable from "material-table";
import EDU_AXIOS from "src/helpers/edu_axios";
import { Formik, Form, Field, ErrorMessage } from "formik";
import * as Yup from "yup";
import notify from "src/helpers/notify";
import EVFormikControl from "src/ev_formik_controls/EVFormikControl";
import Loader from "src/loader/Loader";
let nature = [
  { text: "Main Subject", value: "main" },
  { text: "Optional Subject", value: "optional" },
  { text: "Practical Subject", value: "practical" },
  { text: "Elective-1", value: "elective1" },
  { text: "Elective-2", value: "elective2" },
];
class AllSubjects extends Component {
  constructor(props) {
    super(props);
    let school_id = this.props.match.params.school_id;
    let s_name = "";
    this.state = {
      school_id: school_id,
      subjects: [],
      modalVisibilityAssignClass: false,
      modalVisibilityAddSubject: false,
      modalVisibilityUpdateAssignClass: false,
      modalVisibilityEditSubject: false,
      BlockSubject: "",
      subject_name: "",
      subject_type: "",
      subject_id: "",
      ev_classes: [],
      checkedItems: [],
      arrLength: 0,
      assigned_classes: [],
      loggedInUserId: JSON.parse(localStorage.getItem("user")).uid,
      Edited_Subject: "",
      Edited_ShortCode: "",
      errorsForAddSubject: {},
      errorsForEditSubject: {},
      isShow: false,
    };
  }

  componentWillMount() {
    // render data into table
    this.getSubjects();
    this.getSections();
  }

  // get all subjects of school
  getSubjects = () => {
    let { school_id } = this.state;

    let url = process.env.REACT_APP_API_PATH + "/sm/get-subjects/" + school_id;
    EDU_AXIOS.get(url)
      .then((resp) => {
        //console.log(resp.data.data);
        this.setState({ subjects: resp.data.data });
      })

      .catch((error) => {
        console.log(error);
      });
  };

  getSections = () => {
    let { school_id } = this.state;
    let db_classes = [];
    let url = process.env.REACT_APP_API_PATH + "/cm/get-sections/" + school_id;
    EDU_AXIOS.get(url)
      .then((resp) => {
        console.log(resp.data.data);
        if (resp.data.data.length > 0) {
          for (let i = 0; i < resp.data.data.length; i++) {
            for (let j = 0; j < resp.data.data[i].sections.length; j++) {
              console.log(resp.data.data[i].sections[j]);
              db_classes.push(
                resp.data.data[i].class + "-" + resp.data.data[i].sections[j]
              );
            }
          }
          this.setState({ ev_classes: db_classes });
          this.setState({ isShow: true });
        } else {
          this.setState({ isShow: false });
        }
      })

      .catch((error) => {
        console.log(error);
      });
  };

  // to update assigned classes
  updateAssignedClasses = (school_id, subject_id) => {
    let url =
      process.env.REACT_APP_API_PATH +
      "/sm/get-assigned-classes/" +
      school_id +
      "/" +
      subject_id;
    EDU_AXIOS.get(url)
      .then((resp) => {
        //console.log(resp.data.data);
        this.setState({
          assigned_classes: resp.data.data[0].assigned_classes,
          checkedItems: resp.data.data[0].assigned_classes,
          modalVisibilityUpdateAssignClass: true,
        });
      })

      .catch((error) => {
        console.log(error);
      });
  };
  // hande assignment of the class to subject
  handleClassChange(e) {
    const target = e.target;
    var value = target.value;
    if (target.checked) {
      // add value in array
      this.setState((previousState) => ({
        checkedItems: [...previousState.checkedItems, value],
      }));
      this.setState({ arrLength: this.state.checkedItems.length + 1 });
    } else {
      // remove value from array
      var index = this.state.checkedItems.indexOf(value);
      if (index > -1) {
        this.state.checkedItems.splice(index, 1);
      }
      this.setState({ arrLength: this.state.checkedItems.length });
    }
  }

  // For Assigning class to Subject
  handleAssignClassSubmit(e) {
    e.preventDefault();
    let school_id = this.state.school_id;
    let subject_id = this.state.subject_id;
    let classes = this.state.checkedItems;
    let self = this;
    EDU_AXIOS.post(
      process.env.REACT_APP_API_PATH + "/sm/assign-class-to-subject",
      {
        school_id,
        subject_id,
        classes,
      }
    )
      .then((res) => {
        self.getSubjects();
        self.setState({
          modalVisibilityAssignClass: false,
          modalVisibilityUpdateAssignClass: false,
          checkedItems: [],
        });
        notify("success", res.data.message);
      })
      .catch((error) => {
        console.log(error);
      });
  }

  // formik params
  initialValues = {
    subject_name: "",
    subject_type: "",
  };
  // validation rules of the form fields
  validationSchema = Yup.object().shape({
    subject_name: Yup.string().required("Subject name is required"),
    subject_type: Yup.string().required("Subject type is required"),
  });
  // submit form
  onSubmit = (values, { resetForm }) => {
    let subject = values.subject_name;
    let subject_type = values.subject_type;
    let created_by = this.state.loggedInUserId;
    let created_for = this.state.school_id;
    let self = this;
    EDU_AXIOS.post(process.env.REACT_APP_API_PATH + "/sm/create-subject", {
      subject,
      subject_type,
      created_by,
      created_for,
    })
      .then((res) => {
        self.getSubjects();
        self.setState({ modalVisibilityAddSubject: false });
        resetForm({ values: "" });
        notify("success", res.data.message);
      })

      .catch((error) => {
        console.log(error);
      });
  };

  // formik params
  // validation rules of the form fields
  validationSchemaEdit = Yup.object().shape({
    subject_name: Yup.string().required("Subject name is required"),
    subject_code: Yup.string().required("Short code is required"),
    subject_type: Yup.string().required("Subject type is required"),
  });
  // submit form
  onSubmitEdit = (values) => {
    let subject_id = this.state.subject_id;
    let short_code = this.state.Edited_ShortCode;
    let created_for = this.state.school_id;
    let subject = this.state.subject_name;
    let subject_type = this.state.subject_type;
    console.log(subject_type);
    let self = this;
    EDU_AXIOS.put(process.env.REACT_APP_API_PATH + "/sm/edit-subject", {
      subject_id,
      created_for,
      short_code,
      subject,
      subject_type,
    })
      .then((res) => {
        self.getSubjects();
        self.setState({
          modalVisibilityEditSubject: false,
        });
        notify("success", res.data.message);
      })
      .catch((error) => {
        console.log(error);
      });
  };

  // to edit subject [Is-Subject-Exist]

  isSubjectExist(event) {
    event.preventDefault();

    let subject = this.state.subject_name;
    let created_for = this.state.school_id;
    let self = this;

    EDU_AXIOS.post(process.env.REACT_APP_API_PATH + "/sm/is-subject-exist", {
      subject,
      created_for,
    })
      .then((res) => {
        notify("success", res.data.message);
      })
      .catch((error) => {
        console.log(error);
      });
  }

  // to edit subject [Is-Code-Exist]

  isCodeExist(event) {
    event.preventDefault();

    let short_code = this.state.Edited_ShortCode;
    let created_for = this.state.school_id;

    let self = this;

    EDU_AXIOS.post(process.env.REACT_APP_API_PATH + "/sm/is-short-code-exist", {
      short_code,
      created_for,
    })
      .then((res) => {
        notify("success", res.data.message);
      })
      .catch((error) => {
        console.log(error);
      });
  }

  // to block subject only for the school

  handleBlockSubject(event, subject_id) {
    event.preventDefault();

    let self = this;
    //  alert(subject_id)
    EDU_AXIOS.put(
      process.env.REACT_APP_API_PATH + "/sm/block-unblock-subject",
      {
        subject_id: subject_id,
      }
    )
      .then((res) => {
        self.getSubjects();
        notify("success", res.data.message);
      })
      .catch((error) => {
        console.log(error);
      });
  }

  render() {
    let {
      school_id,
      subjects,
      modalVisibilityAssignClass,
      modalVisibilityAddSubject,
      modalVisibilityUpdateAssignClass,
      modalVisibilityEditSubject,
      Edited_ShortCode,
      subject_name,
      subject_type,
      arrLength,
    } = this.state;

    const columns = [
      {
        title: "Name",
        field: "subject_name",
      },
      {
        title: "Subject Code",
        field: "sort_code",
      },
      {
        title: "Assign Class",
        field: "isClassAssigned",
        render: (data) => {
          if (data.isClassAssigned == false) {
            return (
              <CBadge
                color="secondary"
                style={{ cursor: "pointer" }}
                onClick={
                  data.status == "active"
                    ? () =>
                        this.setState({
                          modalVisibilityAssignClass: true,
                          subject_id: data.subject_id,
                        })
                    : () => {
                        notify("error", "Subject is blocked!");
                      }
                }
              >
                Assign
              </CBadge>
            );
          }
          if (data.isClassAssigned == true) {
            return (
              <CBadge
                color="success"
                style={{ cursor: "pointer" }}
                onClick={
                  data.status == "active"
                    ? () => {
                        this.updateAssignedClasses(school_id, data.subject_id);
                        this.setState({
                          subject_id: data.subject_id,
                        });
                      }
                    : () => {
                        notify("error", "Subject is blocked!");
                      }
                }
              >
                Assigned
              </CBadge>
            );
          }
        },
      },
      {
        title: "Status",
        field: "status",
        render: (data) => {
          if (data.status == "active") {
            return <CBadge color="success">Active</CBadge>;
          }
          if (data.status == "blocked") {
            return <CBadge color="danger">Blocked</CBadge>;
          }
        },
      },
      {
        title: "Type",
        field: "nature",
      },
    ];
    return (
      <CCard>
        <CCardBody>
          <CRow>
            <CCol>
              <NavBar id={school_id} />
            </CCol>
          </CRow>
          {/*
           * Modal box for assign class
           */}

          <CModal
            visible={modalVisibilityAssignClass}
            onDismiss={() =>
              this.setState({
                modalVisibilityAssignClass: false,
                checkedItems: [],
              })
            }
            scrollable
            size="sm"
            keyboard={false}
          >
            <CModalHeader
              onDismiss={() =>
                this.setState({
                  modalVisibilityAssignClass: false,
                  checkedItems: [],
                })
              }
            >
              <CModalTitle>
                <h5>Assign Class</h5>
              </CModalTitle>
            </CModalHeader>

            <CModalBody>
              {this.state.isShow && (
                <>
                  <p>Select classes those you want to assign to the subject </p>
                  <form>
                    <ul style={{ listStyle: "none", marginLeft: "-40px" }}>
                      {this.state.ev_classes.map((item) => (
                        <li>
                          <input
                            type="checkbox"
                            value={item}
                            onChange={(e) => this.handleClassChange(e)}
                            id={item}
                            key={item}
                          />
                          <span
                            style={{
                              fontSize: "16px",
                              marginLeft: "15px",
                            }}
                          >
                            {item}
                          </span>
                        </li>
                      ))}
                    </ul>
                    <input
                      type="hidden"
                      name="sub_id"
                      value={this.state.subject_id}
                    />
                    <br />
                  </form>
                </>
              )}
              {!this.state.isShow && (
                <>
                  <p>
                    Please create classes and at least on section for each class
                  </p>
                </>
              )}
            </CModalBody>
            <CModalFooter>
              <CButton
                color="light"
                onClick={() =>
                  this.setState({
                    modalVisibilityAssignClass: false,
                    checkedItems: [],
                  })
                }
              >
                <FontAwesomeIcon icon={faTimesCircle} /> Cancel
              </CButton>
              <CButton
                color="dark"
                type="submit"
                value="Submit"
                onClick={(e) => this.handleAssignClassSubmit(e)}
                disabled={arrLength < 1}
              >
                <FontAwesomeIcon icon={faSave} /> Save
              </CButton>
            </CModalFooter>
          </CModal>
          {/*End assign class modal*/}
          {/*Modal box to Add a Subject*/}
          <CModal
            visible={modalVisibilityAddSubject}
            onDismiss={() =>
              this.setState({ modalVisibilityAddSubject: false })
            }
          >
            <CModalHeader
              onDismiss={() =>
                this.setState({ modalVisibilityAddSubject: false })
              }
            >
              <CModalTitle>
                <h5>Create Subject</h5>
              </CModalTitle>
            </CModalHeader>
            <Formik
              initialValues={this.initialValues}
              validationSchema={this.validationSchema}
              onSubmit={this.onSubmit}
            >
              {(formik) => (
                <Form className="row g-3">
                  <CModalBody>
                    <CRow className="mb-3">
                      <CCol md={12}>
                        <CFormLabel
                          htmlFor="subject_name"
                          className="required-field"
                        >
                          Subject Name
                        </CFormLabel>
                        <EVFormikControl
                          control="input"
                          type="text"
                          name="subject_name"
                          id="subject_name"
                          placeholder="Select name"
                        />
                      </CCol>
                    </CRow>
                    <CRow>
                      <CCol md={12}>
                        <CFormLabel
                          htmlFor="subject_type"
                          className="required-field"
                        >
                          Subject Type
                        </CFormLabel>
                        <EVFormikControl
                          control="selectwithkey"
                          name="subject_type"
                          id="subject_type"
                          placeholder="Select subject type..."
                          options={nature}
                        />
                      </CCol>
                    </CRow>
                  </CModalBody>
                  <CModalFooter>
                    <CButton
                      color="light"
                      onClick={() =>
                        this.setState({ modalVisibilityAddSubject: false })
                      }
                    >
                      <FontAwesomeIcon icon={faTimesCircle} /> Cancel
                    </CButton>
                    <CButton
                      color="dark"
                      type="submit"
                      value="Submit"
                      disabled={
                        formik.isSubmitting || !formik.dirty || !formik.isValid
                      }
                    >
                      {formik.isSubmitting ? (
                        <Loader />
                      ) : (
                        <>
                          <FontAwesomeIcon icon={faSave} /> Save
                        </>
                      )}
                    </CButton>
                  </CModalFooter>
                </Form>
              )}
            </Formik>
          </CModal>
          {/*End modal to add subject*/}
          {/*
           * Modal box for update assign class
           */}

          <CModal
            visible={modalVisibilityUpdateAssignClass}
            onDismiss={() =>
              this.setState({
                modalVisibilityUpdateAssignClass: false,
                checkedItems: [],
              })
            }
            scrollable
            size="sm"
            keyboard={false}
          >
            <CModalHeader
              onDismiss={() =>
                this.setState({
                  modalVisibilityUpdateAssignClass: false,
                  checkedItems: [],
                })
              }
            >
              <CModalTitle>
                <h5>Assign Class</h5>
              </CModalTitle>
            </CModalHeader>

            <CModalBody>
              <p>Select classes those you want to assign to the subject </p>
              <form>
                <ul style={{ listStyle: "none", marginLeft: "-40px" }}>
                  {this.state.ev_classes.map((item) => (
                    <li>
                      <input
                        type="checkbox"
                        value={item}
                        onChange={(e) => this.handleClassChange(e)}
                        id={item}
                        key={item}
                        defaultChecked={
                          this.state.assigned_classes.includes(item)
                            ? true
                            : false
                        }
                      />
                      <span
                        style={{
                          fontSize: "16px",
                          marginLeft: "15px",
                        }}
                      >
                        {item}
                      </span>
                    </li>
                  ))}
                </ul>
                <input
                  type="hidden"
                  name="sub_id"
                  value={this.state.subject_id}
                />
                <br />
              </form>
            </CModalBody>
            <CModalFooter>
              <CButton
                color="light"
                onClick={() =>
                  this.setState({
                    modalVisibilityUpdateAssignClass: false,
                    checkedItems: [],
                  })
                }
              >
                <FontAwesomeIcon icon={faTimesCircle} /> Cancel
              </CButton>
              <CButton
                color="dark"
                type="submit"
                value="Submit"
                onClick={(e) => this.handleAssignClassSubmit(e)}
                disabled={arrLength < 1}
              >
                <FontAwesomeIcon icon={faSave} /> Save
              </CButton>
            </CModalFooter>
          </CModal>
          {/*End update assign class modal*/}

          {/* Start of Edit Subject Modal*/}
          <CModal
            visible={modalVisibilityEditSubject}
            onDismiss={() =>
              this.setState({ modalVisibilityEditSubject: false })
            }
          >
            <CModalHeader
              onDismiss={() =>
                this.setState({ modalVisibilityEditSubject: false })
              }
            >
              <CModalTitle>
                <h5>Edit Subject</h5>
              </CModalTitle>
            </CModalHeader>
            <Formik
              initialValues={{
                subject_name: subject_name,
                subject_code: Edited_ShortCode,
                subject_type: subject_type,
              }}
              validationSchema={this.validationSchemaEdit}
              onSubmit={this.onSubmitEdit}
              enableReinitialize={true}
            >
              {(formik) => (
                <Form className="row g-3">
                  <CModalBody>
                    <CFormLabel
                      htmlFor="subject_name"
                      className="required-field"
                    >
                      Subject Name
                    </CFormLabel>
                    <CInputGroup className="mb-3">
                      <CCol md={11}>
                        <EVFormikControl
                          control="input"
                          type="text"
                          name="subject_name"
                          id="subject_name"
                          value={formik.values.subject_name}
                          onChange={({ target }) => {
                            this.setState({ subject_name: target.value });
                          }}
                          placeholder="Select name"
                        />
                      </CCol>
                      <CCol md={1}>
                        <CButton
                          type="button"
                          color="dark"
                          id="button-addon2"
                          onClick={(e) => this.isSubjectExist(e)}
                          data-toggle="tooltip"
                          data-placement="top"
                          title="Click to check subject name available or not"
                        >
                          <FontAwesomeIcon icon={faQuestion} />
                        </CButton>
                      </CCol>
                    </CInputGroup>
                    <CFormLabel
                      htmlFor="subject_code"
                      className="required-field"
                    >
                      Short Code
                    </CFormLabel>
                    <CInputGroup className="mb-3">
                      <CCol md={11}>
                        <EVFormikControl
                          control="input"
                          type="text"
                          name="subject_code"
                          id="subject_code"
                          value={formik.values.subject_code}
                          onChange={({ target }) => {
                            this.setState({ Edited_ShortCode: target.value });
                          }}
                          placeholder="Subject code"
                        />
                      </CCol>
                      <CCol md={1}>
                        <CButton
                          type="button"
                          color="dark"
                          id="button-addon2"
                          onClick={(e) => this.isCodeExist(e)}
                          data-toggle="tooltip"
                          data-placement="top"
                          title="Click to check short code available or not"
                        >
                          <FontAwesomeIcon icon={faQuestion} />
                        </CButton>
                      </CCol>
                    </CInputGroup>
                    <CRow mt={3}>
                      <CCol md={12}>
                        <CFormLabel
                          htmlFor="subject_type"
                          className="required-field"
                        >
                          Subject Type
                        </CFormLabel>

                        <Field name="subject_type">
                          {({ field }) => (
                            <CFormSelect
                              {...field}
                              value={formik.values.subject_type}
                              onChange={({ target }) => {
                                this.setState({ subject_type: target.value });
                              }}
                              className={`form-control ${
                                formik.errors.subject_type &&
                                formik.touched.subject_type
                                  ? "is-invalid"
                                  : ""
                              }`}
                            >
                              <option value="">Select...</option>
                              {nature.map((i) => (
                                <option key={i.value} value={i.value}>
                                  {i.text}
                                </option>
                              ))}
                            </CFormSelect>
                          )}
                        </Field>
                        <ErrorMessage
                          name="subject_type"
                          component="div"
                          className="invalid-feedback"
                        />
                      </CCol>
                    </CRow>
                  </CModalBody>
                  <CModalFooter>
                    <CButton
                      color="light"
                      onClick={() =>
                        this.setState({ modalVisibilityEditSubject: false })
                      }
                    >
                      <FontAwesomeIcon icon={faTimesCircle} /> Cancel
                    </CButton>
                    <CButton
                      color="dark"
                      type="submit"
                      value="Submit"
                      disabled={formik.isSubmitting || !formik.isValid}
                    >
                      {formik.isSubmitting ? (
                        <Loader />
                      ) : (
                        <>
                          <FontAwesomeIcon icon={faSave} /> Save
                        </>
                      )}
                    </CButton>
                  </CModalFooter>
                </Form>
              )}
            </Formik>
          </CModal>
          {/*Render data into table*/}
          <MaterialTable
            title={"All Subjects"}
            data={subjects}
            columns={columns}
            onSelectionChange={(rows) => setSelectedRows(rows)}
            options={{
              search: true,
              paging: true,
              filtering: false,
              exportButton: true,
              actionsColumnIndex: -1,
              rowStyle: (x) => {
                if (x.tableData.id % 2) {
                  return { backgroundColor: "#f9f9f9" };
                }
              },
              pageSize: 15,
              emptyRowsWhenPaging: true,
              pageSizeOptions: [15, 30, 45],
            }}
            actions={[
              {
                icon: () => (
                  <CButton
                    color="dark"
                    onClick={() =>
                      this.setState({ modalVisibilityAddSubject: true })
                    }
                  >
                    <FontAwesomeIcon icon={faPlusCircle} /> Add
                  </CButton>
                ),
                tooltip: "Add Subject",
                isFreeAction: true,
              },

              (data) => ({
                icon: () => (
                  <FontAwesomeIcon
                    icon={faLock}
                    style={{ fontSize: "1rem" }}
                    onClick={(e) => this.handleBlockSubject(e, data.subject_id)}
                  />
                ),
                tooltip: "Block Subject",
                onClick: "",
                disabled:
                  data.status === "active" &&
                  data.created_for === this.state.school_id
                    ? false
                    : true,
                hidden: data.status != "active",
              }),
              (data) => ({
                icon: () => (
                  <FontAwesomeIcon
                    icon={faLockOpen}
                    style={{ fontSize: "1rem" }}
                    onClick={(e) => this.handleBlockSubject(e, data.subject_id)}
                  />
                ),
                tooltip: "Unblock Subject",
                onClick: "",
                disabled:
                  data.status === "blocked" &&
                  data.created_for === this.state.school_id
                    ? false
                    : true,
                hidden: data.status === "active",
              }),
              (data) => ({
                icon: () => (
                  <FontAwesomeIcon
                    icon={faEdit}
                    style={{ fontSize: "1rem" }}
                    onClick={() =>
                      this.setState({
                        modalVisibilityEditSubject: true,
                        Edited_ShortCode: data.sort_code,
                        subject_name: data.subject_name,
                        subject_id: data.subject_id,
                        subject_type: data.nature,
                      })
                    }
                  />
                ),
                tooltip: "Edit Subject",
                onClick: "",
                disabled:
                  data.status === "active" &&
                  data.created_for === this.state.school_id
                    ? false
                    : true,
              }),
            ]}
          />
          {/*End table*/}
        </CCardBody>
      </CCard>
    );
  }
}

export default AllSubjects;
